@charset "UTF-8";
body {
  font-family: "";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.header {
  width: 100%;
  padding-top: 3.125rem;
  background: rgb(17, 17, 17);
  position: fixed;
  z-index: 1;
}
.header > .title {
  display: block;
  width: 6.25rem;
  padding-top: 3.125rem;
  position: absolute;
  top: 0;
  left: 1.25rem;
}
.header > .title > img {
  width: 6.25rem;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}
.header > .js-drawer {
  display: block;
  width: 3.125rem;
  padding-top: 3.125rem;
  position: absolute;
  top: 0;
  right: 1.25rem;
  background: rgb(255, 0, 0);
}
@media screen and (min-width: 768px) {
  .header > .js-drawer {
    display: none;
  }
}

.header > .js-drawer > span {
  display: block;
  width: 100%;
  padding-top: 0.1875rem;
  background: rgb(255, 255, 255);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.header > .js-drawer > span::before {
  content: "";
  display: block;
  width: 100%;
  padding-top: 0.1875rem;
  background: rgb(255, 255, 255);
  position: absolute;
  top: -0.8125rem;
  transition: 0.3s;
}
.header > .js-drawer > span::after {
  content: "";
  display: block;
  width: 100%;
  padding-top: 0.1875rem;
  background: rgb(255, 255, 255);
  position: absolute;
  top: 0.8125rem;
  transition: 0.3s;
}
.header > .js-drawer-open > span::before {
  transform: rotate(45deg);
  transform-origin: center;
  top: 0;
}

.header > .js-drawer-open > span {
  background: rgba(255, 255, 255, 0);
}

.header > .js-drawer-open > span::after {
  transform: rotate(-45deg);
  transform-origin: center;
  top: 0;
}

.js-drawer-menu {
  display: block;
  background: rgb(255, 105, 180);
  width: 100%;
  height: 100vh;
  position: absolute;
  top: -100%;
  left: 0;
  padding: 4.375rem 0;
  transition: 0.3s;
}
.js-drawer-menu > a {
  display: block;
  padding: 0.625rem 0;
  margin-bottom: 0.3125rem;
}
.js-drawer-menu > a > li {
  text-align: center;
}
.js-drawer-menu-open {
  top: 0;
}

.global-menu {
  display: none;
}
@media screen and (min-width: 768px) {
  .global-menu {
    display: flex;
    height: 100%;
    position: absolute;
    top: 0;
    right: 1.25rem;
    color: rgb(255, 255, 255);
  }
}

@media screen and (min-width: 768px) {
  .global-menu > a,
  .global-menu > a > li {
    display: inherit;
    height: inherit;
    align-items: center;
  }
}

@media screen and (min-width: 768px) {
  .global-menu > a {
    display: inherit;
    padding: 0 1.25rem;
  }
}

.global-menu-inquiry {
  position: relative;
  overflow: hidden;
}

.global-menu-inquiry > li {
  position: relative;
  z-index: 1;
}

.global-menu-inquiry::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  background: rgb(255, 105, 180);
  position: absolute;
  top: 0;
  left: -100%;
  transition: 0.3s;
}

.global-menu-inquiry:hover::before {
  left: 0;
}